import React, { Fragment, PureComponent } from 'react'
import last from 'lodash/last'

import Page from 'containers/NoticePage'
import StarRater from 'containers/StarRater'
import storingHoc from 'containers/MemoPage/storingHoc'

class NoticePage extends PureComponent {
  state = {
    rate: false
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ rate: true })
    }, 3 * 1000)
  }

  render() {
    const { memos } = this.props
    return (
      <Fragment>
        <Page memo={last(memos)} />
        {this.state.rate && <StarRater title="製作備忘錄有幫助你了解看診前的注意事項嗎？" rateKey="memo" />}
      </Fragment>
    )
  }
}

export default storingHoc(NoticePage)

